@import 'variables';

html{
    font-size: 10px;
    font-family: $main-font;
    max-width: 1900px;
    margin: auto;
}

body{
    background-color: $bgc-color;
}

.interlude{
    background-image: url(../assets/images/bg_interlude.jpg);
    background-attachment: fixed;
    padding: $spacing-mobile * 3;

    @media (min-width: 1024px){
        display: grid;
        grid-template-columns: 1fr 1fr;
    }

    &__text{
        text-align: center;
        font-family: $menu-font;
        font-size: 2.4rem;
        color: #ddd;
        margin-bottom: $spacing-mobile;

        @media (min-width: 1024px){
            text-align: right;
            padding: 10px;
            font-size: 2.8rem;
            margin-right: 15px;
            margin-top: $spacing-mobile;

            &--logged{
                text-align: left;
            }
        }
    }
    
    &__btn{
        display: block;
        margin: auto;
        margin-bottom: $spacing-mobile * 2;
        padding: 10px;
        border: none;
        border-radius: 35px;
        cursor: pointer;
        background-color: #9A00E7;
        transition: .2s;

        @media (min-width: 1024px){
            margin-bottom: $spacing-mobile;
            margin-left: 15px;
        }
      

        &:hover{
            transform: scale(1.1);
        }

        a{
            text-decoration: none;
            font-family: $menu-font;
            font-size: 2rem;
            color: #eee;

            @media (min-width: 1024px){
                font-size: 2.8rem;
            }
        }
    }
}
