@import '../variables';

.about{
    padding-top: $spacing-mobile * 3;
    position: relative;
    top: 10vh;
    color: white;
    background-color: black;
    -webkit-box-shadow: 0px 19px 27px -17px rgba(0, 0, 0, 1);
    -moz-box-shadow: 0px 19px 27px -17px rgba(0, 0, 0, 1);
    box-shadow: 0px 19px 27px -17px rgba(0, 0, 0, 1);
    

    @media(min-width: 1024px){
        display: flex;
        align-content: center;
        justify-content: center;
        padding-top: $spacing-mobile * 6;
    }

    &__text-container{
        margin-left: $spacing-mobile;
        margin-right: $spacing-mobile;
        margin-bottom: $spacing-mobile;

        @media(min-width: 1024px){
            flex-basis: 40%;
            margin-left: $spacing-mobile * 2;
            margin-right: $spacing-mobile * 2;
            margin-bottom: $spacing-mobile * 2;
    
        }

        .about__title{
            display: inline-block;
            font-size:2.8rem; 
            letter-spacing:1px; 
            text-transform:uppercase; 
            margin-bottom: $spacing-mobile;
            color: #eee;
        }

        .about__text-description{
            font-size: 1.3rem;
            margin-bottom: 20px;
            color: #eee;
            
            strong{
                color: $special-fontcolor
            }

            @media (min-width: 640px){
                font-size: 1.5rem;
            }
        }
    }

    &__image-container{
        @media(min-width: 1024px){
            flex-basis: 40%;
            display: flex;
            align-content: center;
        }
        img{
            width: 100%;

            @media(min-width: 1024px){
                margin-left: 10%;
                width: 80%;
            }
        }
    }
}